
import { Nullable } from '../../../shared/domain/Nullable';
import { GameId } from '../../shared/domain/Games/GameId';
import { Game } from '../domain/Game';
import { GameRepository } from '../domain/GameRepository';
import { CreateGameRequest } from './CreateGameRequest';
import { UpdateGameRequest } from './UpdateGameRequest';
import { GameFilter } from '../../shared/domain/Games/GameFilter';

export class GameCreator {
  private repository: GameRepository;

  constructor(repository: GameRepository) {
    this.repository = repository;
  }

  async getGames(filter: GameFilter): Promise<Nullable<Game[]>> {
    return this.repository.find(filter) || null;
  }

  async getGame(id: GameId): Promise<Nullable< Game >> {
    return this.repository.findOne(id) || null;
  }

  async insertGame(request: CreateGameRequest): Promise<void> {
    const game = new Game(
      /* todo: agrega los campos de tu entidad */
    );
    return this.repository.create(game);
  }

  async updateGame(request: UpdateGameRequest): Promise<void> {
    const game = new Game(
      /* todo: agrega los campos de tu entidad */
    );
    return this.repository.update(new GameId(request.id), game);
  }

  async deleteGame(id: GameId): Promise<void> {
    return this.repository.deleteOne(id);
  }
}
